    DetailReportAudioComponent.prototype.urlComposer = function (ext) {
        var recordSelection = JSON.parse(localStorage.getItem("selected_row"));
        var url = recordSelection.record;
        var route = url.substring(url.lastIndexOf("/") + 1);
        var fileExtension = route.substring(route.lastIndexOf(".") + 1);
        var fileName = this.env.loopbackApiUrl + "/audio/" + route;
        if (ext) {
            fileName = fileName + "." + ext;
        }
        // console.error("fileName", fileName);
        return fileName;
    };
    DetailReportAudioComponent.prototype.downloadFile = function (ext) {
        var _this = this;
        var recordSelection = JSON.parse(localStorage.getItem("selected_row"));
        var url = recordSelection.record;
        var route = url.substring(url.lastIndexOf("/") + 1);
        var fileExtension = route.substring(route.lastIndexOf(".") + 1);
        var src = recordSelection.call_source;
        var dst = recordSelection.call_destiny;
        var fileName = recordSelection.call_type + "-" + recordSelection.agent_name + "-" + recordSelection.start_date + "-" + recordSelection.start_time + "-" + src + "-" + dst + "." + fileExtension;
        if (ext) {
            fileName = fileName + "." + ext;
        }
        this.callsDetailService.downloadFile(route, fileName).subscribe(function (response) {
            var dataType = response.type;
            var binaryData = [];
            binaryData.push(response);
            var downloadLink = document.createElement("a");
            downloadLink.href = window.URL.createObjectURL(new Blob(binaryData, { type: dataType }));
            if (fileName)
                downloadLink.setAttribute("download", fileName);
            document.body.appendChild(downloadLink);
            downloadLink.click();
        }, function (error) {
            console.error("Error", error);
            _this.show = false;
            _this.alertService.error(error.status);
            _this.alertMessage.alertTitle = "Error del servidor";
            _this.alertMessage.alertText = error.statusText;
            _this.alertMessage.alertShow = true;
            _this.alertMessage.alertClass =
                "alert alert-danger alert-dismissible fade show";
        });
    };
